import React from 'react';
import PropTypes from 'prop-types';
import {PanelHeaderBack, Input, Panel, PanelHeader, HeaderButton, Search, Group, Footer, List, Cell, View, FixedLayout} from '@vkontakte/vkui';
import Progress from "./Progress";
import { ListBoxItem, ListBox, YMaps, Map, Placemark } from 'react-yandex-maps';
import '../styles/main.css';

export default class MyMap extends React.Component {
    constructor (props) {
        super(props);
        this.props = props;
        this.state = {
            plastic: false,
            paper: false,
            sop: false
        }
        this.onChangeState = this.onChangeState.bind(this);
    }

    onChangePoints() {

    }

    onChangeState(props) {
        if (props === "plastic") {
            let isPlasic = !this.state.plastic;
            this.setState({plastic: isPlasic});
        }
        if (props === "paper") {
            let isPaper = !this.state.paper;
            this.setState({paper: isPaper});
        }
        if (props === "sop") {
            let isSop = !this.state.sop;
            this.setState({sop: isSop});
        }

        // onChangePoints();
    }
    render() {
        return (
            <View id={this.props.id} activePanel="mymap">
                <Panel id="mymap">
                    <PanelHeader>Карта </PanelHeader>
                    <FixedLayout vertical="top bottom">
                        <YMaps>
                                <Map className="all_window"
                                    defaultState = {{ 
                                        center: [55.751574, 37.573856], 
                                        zoom: 9 
                                    }}
                                >
                                    <ListBox data = {{ content: "Что относим на свалку?" }}>
                                        
                                        <ListBoxItem    onClick={() => this.onChangeState("plastic")} 
                                                        data = {{ content: "Пластик" }} />
                                        <ListBoxItem    onClick={() => this.onChangeState("paper")}
                                                        data = {{ content: "Бумагу" }} />
                                        <ListBoxItem    onClick={() => this.onChangeState("sop")}
                                                        data = {{ content: "Соперников" }} />
                                    
                                    </ListBox>
                                </Map>
                        </YMaps>
                    </FixedLayout>
                </Panel>
            </View>
        )
    }
}